@import "./../../info-page.scss";
@import "./../../banner.scss";
@import "./../../tabs-block.scss";
@import "./../../statistics.scss";

.wholesale-and-corporate-clients {
  .page-banner.page-banner_big {
    img {
    }
    .banner-container {
      justify-content: flex-start;
      margin-top: 220px;
      @media (max-width: $tablet) {
        margin-top: 0;
        justify-content: center;
      }
      &__icon-eco {
      }
      &__title {
        display: block;
        margin-top: 26px;
      }
      &__description {
        margin-top: 30px;
        line-height: 20px;
      }
    }
  }

  .page-banner.page-banner_medium {
    img {
    }
    .banner-container.container {
      justify-content: flex-start;
      margin-top: 240px;
      @media (max-width: $tablet) {
        margin-top: 0;
        justify-content: center;
      }

      &__icon-eco {
      }
      &__title {
        margin-top: 20px;
      }
      &__description {
        
      }
    }
  }

  .text-block-with-tabs-block {
    padding-top: 83px;
    padding-bottom: 90px;
    @media (max-width: $tablet) {
      padding-top: 45px;
      padding-bottom: 45px;
    }
    @media (max-width: $mobile) {
      padding-top: 30px;
      padding-bottom: 30px;
    }
    .text-block-with-tabs-block__text-block {
      @media (max-width: 1150px) {
        flex-direction: column;
      }
      display: flex;
      justify-content: space-between;
      .text-block-with-tabs-block__title {
        @include rale-800-45;
        max-width: 410px;
        line-height: 52px;
        display: block;
        @media (max-width: 1150px) {
          max-width: none;
          text-align: center;
        }
      }
      .text-block-with-tabs-block__description-blocks {
        flex: 0 0 59.6%;
        display: flex;
        justify-content: space-between;
        gap: 20px;
        @media (max-width: 1150px) {
          flex: auto;
          justify-content: space-around;
          margin-top: 20px;
        }
        @media (max-width: 750px) {
          flex-direction: column;
          align-items: center;
        }
        .text-block-with-tabs-block__description-block {
          &:first-child {
            max-width: 355px;
            width: 100%;
            align-self: start;
          }
          &:last-child {
            max-width: 355px;
            width: 100%;
            align-self: flex-end;
          }
        }
      }
      .text-block-with-tabs-block__description-block {
        max-width: 330px;
        display: flex;
        flex-direction: column;
        padding-top: 10px;
        gap: 24px;
        .text-block-with-tabs-block__description {
        }
      }
    }
    .tabs-block {
      padding-top: 130px;
      padding-bottom: 0px;
      @media (max-width: $tablet) {
        padding-top: 45px;
      }
      @media (max-width: $mobile) {
        padding-top: 30px;
      }
      hr {
        border-color: rgba($color: #000000, $alpha: 0.08);
      }
      &__container {
        .tab-content-block {
          padding-top: 35px;
          @media (max-width: $tablet) {
            padding-top: 30px;
          }
          @media (max-width: $mobile) {
            padding-top: 20px;
          }
        }
        .tab-bar {
          max-width: none;
          justify-content: space-between;
          @media (max-width: 450px) {
            display: grid;
            grid-template-columns: repeat(3, 1fr);
            grid-template-rows: 80px;
            row-gap: 10px;
          }
        }
        .tabs-block__text-block-with-img {
          gap: 20px;
          .tabs-block__text-block {
            padding-top: 30px;
            width: 100%;
            max-width: 450px;
            min-width: 330px;
            @media (max-width: $mobile) {
              min-width: 0;
              padding-top: 0px;
            }
            .rectangle.background {
              margin-top: 40px;
              padding: 17px 44px 17px 43px;
              @media (max-width: $tablet) {
                margin-top: 30px;
              }
              @media (max-width: $mobile) {
                margin-top: 20px;
              }
            }
          }
          .tabs-block__img-block {
            width: 100%;
          }
          .tabs-block__text-blocks {
            gap: 24px;
            .tabs-block-icon-text-block__text {
            }
          }
          img {
          }
        }
        .tabs-block__text-block-with-img {
          .tabs-block__text-block {
          }
          .tabs-block__img-block {
            max-width: 761px;
            width: 100%;
          }
          .tabs-block__text-blocks {
            .tabs-block-icon-text-block__text {
            }
          }
          img {
            width: 100%;
            height: 100%;
            object-fit: cover;
          }
        }
        .tab-bar-item.tab-button {
          padding-bottom: 15px;
          text-align: start;
          line-height: 24px;
          @media (max-width: 1100px) {
            flex-direction: column;
            align-items: center;
            justify-content: end;
            text-align: center;
            @media (max-width: 770px) {
              font-size: 15px;
              line-height: 20px;
            }
            @media (max-width: 600px) {
              font-size: 10px;
              height: 100%;
              padding-bottom: 0;
              gap: 5px;
            }
          }
          .icon-sweets {
            font-size: 43px;
            @media (max-width: $mobile) {
              font-size: 25px;
            }
          }
          .icon-supermarket {
            font-size: 37px;
            @media (max-width: $mobile) {
              font-size: 25px;
            }
          }

          .icon-shop {
            font-size: 37px;
            @media (max-width: $mobile) {
              font-size: 25px;
            }
          }
          .icon-dinner-table {
            font-size: 49px;
            @media (max-width: $mobile) {
              font-size: 25px;
            }
          }
          .icon-dumbbell {
            font-size: 45px;
            @media (max-width: $mobile) {
              font-size: 25px;
            }
          }
          &:hover {
          }
          &:active {
          }
          &:focus {
          }
        }
      }
    }
  }
}
